// Fetch the HTML file and load it into the DOM
fetch("public/index.html")
  .then((response) => response.text())
  .then((html) => {
    // Render the HTML content
    document.getElementById("root").innerHTML = html;

    // Fetch the image file
    return fetch("public/favicon/Inspiremind.png");
  })
  .then((response) => response.blob())
  .then((blob) => {
    // Create a URL for the blob
    const imageUrl = URL.createObjectURL(blob);
    // Create an image element
    const imageElement = document.createElement("img");
    imageElement.src = imageUrl;
    imageElement.alt = "imgs";

    // Replace the image placeholder with the fetched image
    document.querySelector(".hero img").replaceWith(imageElement);

    // Render the React app
    // ReactDOM.render(<App />, document.getElementById("root"));
  })
  .catch((error) => {
    console.error("Error:", error);
  });
